import PropTypes from 'prop-types';

export default PropTypes.shape({
  id: PropTypes.number,

  name: PropTypes.string,
  dim: PropTypes.number,
  color: PropTypes.string,
});

export const formValues = PropTypes.shape({
  name: PropTypes.string,
  dim: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  color: PropTypes.string,
});

export const formTouched = PropTypes.shape({
  name: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  dim: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  color: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
});

export const formErrors = PropTypes.shape({
  name: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  dim: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  color: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
});
